        headmoveover() {
            var can_look_height = window.innerHeight
            if (can_look_height <= 1080) {
                document.getElementById("headmenu").style.top = "0px"
            }
        },


        delParam(paramKey) {
            var url = window.location.href;    //url
            var urlParam = window.location.search.substring(1);   //
            var beforeUrl = url.substring(0, url.indexOf("?"));   //
            var nextUrl = "";

            var arr = new Array();
            if (urlParam != "") {
                var urlParamArr = urlParam.split("&"); //&
                for (var i = 0; i < urlParamArr.length; i++) {
                    var paramArr = urlParamArr[i].split("="); //
                    //
                    if (paramArr[0] != paramKey) {
                        arr.push(urlParamArr[i]);
                    }
                }
            }
            if (arr.length > 0) {
                nextUrl = "?" + arr.join("&");
            }
            url = beforeUrl + nextUrl;
            return url;
        },

        //cookies
        getck(sname) {
            var acookie = document.cookie.split("; ");
            try {
                for (var i = 0; i < acookie.length; i++) {
                    var arr = acookie[i].split("=");
                    if (sname == arr[0]) {
                        if (arr.length > 1)
                            return arr[1];
                        else
                            return false;
                    }
                }
                return false;
            }
            catch (e) {
                console.log('cookie')
            }
        },


        /**
        * @showMessage
        * @desc TDesignMessage
        * @param
        * @type info 
        * @type success 
        * @type warning 
        * @type error 
        * @type question 
        * @type loading 
        * @content content = () => {return (<div></div>)}
        */
        showMessage(type, time, content) {
            if (time) {
                time = 3000
            }
            if (type == 'info') {
                MessagePlugin.info({ duration: time, content: content })
            }
            if (type == 'success') {
                MessagePlugin.success({ duration: time, content: content })
            }
            if (type == 'warning') {
                MessagePlugin.warning({ duration: time, content: content })
            }
            if (type == 'error') {
                MessagePlugin.error({ duration: time, content: content })
            }
            if (type == 'question') {
                MessagePlugin.question({ duration: time, content: content })
            }
            if (type == 'loading') {
                MessagePlugin.loading({ duration: time, content: content })
            }
            if (type == 'close') {
                MessagePlugin.closeAll()
            }
        },

        /**
        * @getQueryVariable
        * @desc 
        * @param id 
        */
        getQueryVariable(variable) {
            var query = window.location.search.substring(1);
            var vars = query.split("&");
            for (var i = 0; i < vars.length; i++) {
                var pair = vars[i].split("=");
                if (pair[0] == variable) { return pair[1]; }
            }
            return (false);
        },
    },

}
</script>
  
